.unactive {
  display: block;
}

.det-mob {
  gap: 50px;
}

.tasks {
  max-width: 320px;
  margin: 0 auto 16px;
  display: block;
}

.tesks-cont {
  padding: 0 16px;
}

.tesks-list-item {
  border: 1px solid #b4f5ff;
  border-radius: 12px;
  align-items: center;
  gap: 8px;
  padding: 8px 4px;
  display: flex;
}

.tesks-list-item + .tesks-list-item {
  margin-top: 4px;
}

.answer_variants {
  align-items: center;
  gap: 10px;
  padding-left: 10px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.4;
  display: flex;
}

.answ-text {
  width: 236px;
  border-bottom: 1px solid #0c0c0c;
  padding: 8px;
}

.correct {
  background-color: #f6fff3;
  border-bottom: 1px solid #58d513;
  border-radius: 12px;
}

.wrong {
  background-color: #fff3f7;
  border-bottom: 1px solid #ffa8a8;
  border-radius: 12px;
}

.cheack-castom {
  width: 24px;
  height: 24px;
  border: 2px solid #313b5c;
  border-radius: 99px;
}

.opened-quest {
  align-items: center;
  gap: 8px;
  margin-bottom: 16px;
  display: flex;
}

.tesks-list-item-title {
  background-color: #f1f4f9;
  border: none;
}

.tesks-disc {
  font-size: 12px;
  font-weight: 400;
  line-height: 1.4;
}

.tesks-svg {
  width: 44px;
  height: 44px;
  border: 1px solid #b4f5ff;
  border-radius: 50px;
  padding: 8px;
}

.quest {
  width: 135px;
}

.mark {
  border: 1px solid #e9fcff;
  border-radius: 12px;
  padding: 12px 8px;
}

.open {
  transform: rotate(180deg);
}

.incorrect {
  border: 1px solid #fec204;
  display: block;
}

.incorrect_ans {
  background-color: #fcffe9;
  border: 1px solid #fec204;
}

.edit-btn {
  width: 224px;
  text-align: center;
  background-color: #fff;
  border: 4px solid #dfe1f6;
  border-radius: 20px;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  margin-top: 16px;
  padding: 8px 24px;
  display: flex;
}

.edit-svg {
  width: 24px;
  height: 24px;
}

.unactive-time {
  background-color: #e9fcff;
  border-radius: 20px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.4;
  display: none;
}

.unactive-time span {
  margin-left: 70px;
}

@media screen and (min-width: 768px) {
  .det-mob {
    gap: 40px;
  }

  .class-rate-disc {
    padding-top: 22px;
  }

  .tasks {
    max-width: 768px;
    margin: 0 auto 32px;
    display: block;
  }

  .tesks-cont {
    padding: 0 24px;
  }

  .tesks-list-item {
    gap: 20px;
    padding: 12px 16px;
  }

  .tesks-disc {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.4;
  }

  .tesks-svg {
    width: 40px;
    height: 40px;
  }

  .quest {
    width: 544px;
  }

  .unactive-time {
    display: block;
  }

  .student-details {
    justify-content: space-between;
    align-items: center;
    margin-top: 28px;
    display: flex;
  }

  .answers_list {
    flex-wrap: wrap;
    gap: 32px;
    display: flex;
  }

  .cheack-castom {
    display: none;
  }

  .answer_variants {
    padding-left: 0;
    font-size: 16px;
    font-weight: 500;
  }

  .answ-text {
    width: 327px;
    padding: 12px 8px;
  }

  .opened-quest {
    gap: 20px;
    margin-bottom: 28px;
  }

  .edit-btn {
    width: 260px;
    margin-top: 0;
    padding: 12px 24px;
  }
}

@media screen and (min-width: 1440px) {
  .tasks {
    max-width: 940px;
  }

  .tesks-cont {
    padding: 0 20px;
  }

  .quest {
    width: 696px;
  }

  .student-details {
    margin-top: 32px;
  }

  .answers_list {
    gap: 20px;
  }

  .answ-text {
    width: 410px;
  }

  .opened-quest {
    margin-bottom: 32px;
  }
}

